$xl: 1499px;
$lg: 1199px;
$md: 1024px;
$sm: 767px;
$xs: 479px;
// colors
$text: #3C3D3F;
$text-light: #ffffff;
$blue: #B7C7D8;
$green: #CED2C9;
// fonts
$mainFont: 'Aeonik', sans-serif;
//


@mixin itemGrid($n, $gap) {
  width: calc(100% / #{$n} - #{$gap}px + #{$gap}px / #{$n});
  margin-right: #{$gap}px;
  &:nth-child(#{$n + 1}n){
    margin-right: #{$gap}px;
  }
  &:nth-child(#{$n}n){
    margin-right: 0;
  }
}



@mixin vertical-borders-single($top, $right, $bottom) {
  padding-top: $top;
  padding-bottom: $bottom;
  &::after {
    right: $right;
    top: 0;
    bottom: 0;
    position: absolute;
    content: '';
    width: 1px;
    background: $text;
    display: flex;
    bottom: $bottom;
    top: $top;
  }
}

@mixin vertical-borders($top, $right, $bottom) {
  padding-top: $top;
  padding-bottom: $bottom;
  &::after {
    right: $right;
    top: 0;
    bottom: 0;
    position: absolute;
    content: '';
    width: 1px;
    background: $text;
    display: flex;
  }
  &:nth-of-type(odd) {
    &::after {
      bottom: $bottom;
    }
  }
  &:nth-of-type(even) {
    &::after {
      top: $top;
    }
  }
}

